.section-nav {
  padding-left: 0;
  border-left: 1px solid #ffc107;
}
.section-nav ul {
  padding-left: 1.25rem;
}
.toc-entry {
  display: block;
}
.toc-entry a {
  display: block;
  padding: 0.125rem 1.5rem;
  color: #99979c !important;
}
.bd-toc {
  position: sticky !important;
  top: 2rem;
  height: calc(100vh - 4rem);
  overflow-y: auto;

  order: 2;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  font-size: 0.875rem;
  /* transition: opacity 0.6s ease; */
}
